    @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
    *{
        margin: 0;
        padding: 0;
        font-family: 'Montserrat', sans-serif;
    }
    .app{
        position: relative;
        background: rgb(236, 236, 236);
    }

    .intro{
        height: 100vh;
        background: #061426;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 30px;
        span{
            color: #20e5ff;
        }
        :nth-child(2){
            font-style: italic;
            font-size: 25px;
            color: #0d5b6d
        }
        
    }

    .navbar{
        top:0;
        position: fixed;
        color: white;
        background: #043540;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 2;
        height: 50px;
        width: 100vw;
        
        .nav_container{
            display: flex;
            // margin-left: 20%;
            width: 30%;
            justify-content: space-between;
            div{
                cursor: pointer;
            }
        }
        
    }

    .about{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        // height: 100vh;
        h1{
            border-bottom: 2px solid black;
            margin-bottom: 50px;
            margin-top: 50px;
            padding-bottom: 10px;
        }

        .about_container{
            width: 100%;
            height: 100%;
            display: grid;
            grid-template-columns: 50% 50%;
            gap: 5%;

            .profile_container{

                .profile_pic{
                    float: right;
                    margin-right: 50px;
                        background: url("/image/nerolow.png");
                        background-size:100% 100%;
                        background-repeat: no-repeat;
                        min-height: 40vh;
                        width: 300px;
                        
                }
            }
            .skill_container{
                width: 80%;
                .info_title{
                    font-size: 24px;
                    font-weight: bold;
                    margin-bottom: 20px;
                }
                .info_details{
                    width: 65%;
                }
                .info_skill{
                    margin-top: 40px;
                    font-size: 1em;
                    font-weight: bolder;

                }
                .logos{
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr 1fr;
                    gap: 10px;
                    font-size: 40px;
                    width:60%;
                    margin-top: 20px;
                    .mongodb_logo{
                        background: url("/image/mongodb.png");
                        height: 40px;
                        width: 40px;
                        background-size: cover;
                    }
                    .django_logo{
                        background: url("/image/django.png");
                        height: 40px;
                        width: 40px;
                        background-size: cover;
                    }
                }
            }
        }
        
    }

    .portfolio{
        display: flex;
        flex-direction: column;
        align-items: center;
        
        .modal{
            position: fixed;
            display: flex;
            flex-direction: column;
            align-items: center;
            top:0;
            left:0;
            height: 100vh;
            width: 100vw;
            z-index: 3;
            background: rgb(black,0.3);
            opacity: 0;
            transition: 0.5s;
            pointer-events: none;
            
            .modal_content{
                margin-top: 5%;
                position: relative;
                background: white;
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 60%;
                height: 80%;
                transition: 0.5s;
                .inner_content{
                    display:grid;
                    height: 100%;
                    width: 100%;
                    grid-template-rows: 60% 5% 35%;
                    overflow: hidden;
                    :nth-child(1){
                        position: relative;
                        .right{
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            font-weight: bold;
                            font-size: 20px;
                            padding: 10px 10px;
                            background: rgb(255,255,255,0.4)
                        }
                        .left{
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            padding: 10px 10px;
                            font-weight: bold;
                            font-size: 20px;
                            background: rgb(255,255,255,0.4)
                        }
                        .carousel{
                            position:relative;
                            transition: 0.5s;
                            div{ 
                                height:100%; 
                                width: 100%;
                            }
                            :nth-child(1){
                                    background-image: url("../image/Capture.PNG");
                                    background-repeat: no-repeat;
                                    background-size: 100% 100%;
                            }
                            :nth-child(2){
                                    background-image: url("../image/Capture2.PNG");
                                    background-repeat: no-repeat;
                                    background-size: 100% 100%;
                                    position: absolute;
                                    top: 0;
                                    right: -100%;
                            }
                            :nth-child(3){
                                    background-image: url("../image/Capture3.PNG");
                                    background-repeat: no-repeat;
                                    background-size: 100% 100%;
                                    position: absolute;
                                    top: 0;
                                    right: -200%;
                            }
                            :nth-child(4){
                                    background-image: url("../image/Capture4.PNG");
                                    background-repeat: no-repeat;
                                    background-size: 100% 100%;
                                    position: absolute;
                                    top: 0;
                                    right: -300%;
                            }
                            }
                        
                    }
                    :nth-child(1){
                        height:100%;
                        width: 100%;
                    }
                }
                .close_modal{
                    position: absolute;
                    top:0;
                    right: 5px;
                    font-size: 2em;
                    z-index: 4;
                }
            }
            .modal_content2{
                margin-top: 5%;
                position: relative;
                background: white;
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 0;
