                            background: rgb(255,255,255,0.4)
                        }
                        .left{
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            padding: 10px 10px;
                            font-weight: bold;
                            font-size: 20px;
                            background: rgb(255,255,255,0.4)
                        }
                        .carousel{
                            position:relative;
