                            }
                            :nth-child(2){
                                background-image: url("../image/image5.jpg");
                                background-repeat: no-repeat;
                                background-size: 100% 100%;
                                position: absolute;
                                top: 0;
                                right: -100%;
                            }
                            :nth-child(3){
                                background-image: url("../image/Image6.jpg");
                                background-repeat: no-repeat;
                                background-size: 100% 100%;
                                position: absolute;
                                top: 0;
                                right: -200%;
                            }
                            :nth-child(4){
                                background-image: url("../image/Capture4.PNG");
                                background-repeat: no-repeat;
                                background-size: 100% 100%;
                                position: absolute;
                                top: 0;
                                right: -300%;
                            }
                            }
                        
                    }

                    :nth-child(1){
                        height:100%;
                        width: 100%;
                    }
                }
                .close_modal{
                    position: absolute;
                    top:0;
