                }
            }
            .modal_content_details{
                display: flex;
                flex-direction: column;
                align-items: center;
                div{
                    margin-top: 5%;
                    text-align: center;
                    font-weight: bold;
                    font-size: 24px;
                    padding-bottom: 5px;
                }
                p{
                    margin-top: 30px;
                    width: 80%;
                    text-align: center;
                }
                i{
                    margin-top: 20px;
                    font-size: 48px;
                    cursor: pointer;
                }
                i:hover{
                    color: #20e5ff
                }
            }
        }
        h1{
            border-bottom: 2px solid black;
            padding-bottom: 10px;
            margin-bottom: 50px;
            margin-top: 50px;
        }
        .project_container{
            display: grid;
            grid-template-columns: 1fr 1fr;
            width: 50%;
            margin-bottom: 50vh;
            gap:20px;
            
        .carousel_container1{
                overflow: hidden;
                position: relative;
                
