    .contact{
        display: flex;
        flex-direction: column;
        align-items: center;
        .triangle{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap:0;
            overflow: hidden;
            :nth-child(1){
                margin: 0;
                width: 0;
                height: 0;
                border-bottom:80px solid #074259;
                border-left: 50vw solid transparent;
            }
            :nth-child(2){
                margin: 0;
                width: 0;
                height: 0;
                border-bottom: 80px solid#074259;
                border-right: 50vw solid transparent;
            }
        }
        .contact_detail{
            background: #074259;
            width: 100%;
            text-align: center;
            height: 50vh;
            color: white;
            display: flex;
            flex-direction: column;
            align-items: center;

            .contact_title{
                font-size: 36px;
                font-weight: bold;
                padding-bottom: 10px;
                border-bottom: white 3px solid;
                margin-bottom: 50px;
            }
            .logo{
                margin-top: 50px;
                text-align: center;
                font-size: 60px;
                display: flex;
                a{
                    margin: 0 20px 0 20px;
                }
                .fas,
                .fab{
                    color: white;
                }

                .fas:hover,
                .fab:hover{
                    color: #20e5ff;
                }
            }
        }
    }

    @keyframes frame{
        0%{
            transform: translateX(0%)
        }
        15%{
            transform: translateX(0%)
        }

        25%{
            transform: translateX(-100%)
        }
        40%{
            transform: translateX(-100%)
        }
        55%{
            transform: translateX(-200%)
        }
        65%{
            transform: translateX(-200%)
        }
        75%{
            transform: translateX(-300%)
        }
        90%{
            transform: translateX(-300%)
        }
        100%{
            transform: translateX(0%)
        }
    }


    @keyframes frame2{
        0%{
            transform: translateX(0%)
        }
        20%{
            transform: translateX(0%)
        }

        33%{
            transform: translateX(-100%)
        }
        53%{
            transform: translateX(-100%)
        }
        66%{
            transform: translateX(-200%)
        }
        86%{
            transform: translateX(-200%)
        }
        100%{
            transform: translateX(0%)
        }
    }

    @media only screen and (max-width: 1200px) {
        body {
          background-color: lightblue;
        }
        .navbar{
            .nav_container{
                width: 50%;                
            }
        }

        .about{
        .about_container{
            
            .profile_container{

                .profile_pic{
                        // background-size:80% 80%;
                        height: 80%            
                }
            }
            .skill_container{
                width: 80%;
                .info_details{
                    width: 100%;
                }
      }
    }
}
    .portfolio{
        .modal{
            .modal_content{
                width: 60%;
                height: 80%;

        }
    }
        .project_container{
            grid-template-columns: 1fr;
        }
    }
    }


    @media only screen and (max-width: 768px) {
        body {
          background-color: lightblue;
        }
        .navbar{
            .nav_container{
                width: 70%;                
            }
        }
        .about{
            .about_container{
                display: flex;
                flex-direction: column;
                align-items: center;
                .profile_container{
                    display: flex;
                    width: 100%;
                    justify-content: center;
                    .profile_pic{
                        height: 60vh;
                    }
                }
                .skill_container{
                .logos{
                    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
                    gap: 30px;
             }
            }
        }
    }
        .portfolio{
            margin-top: 50px;
            .modal{
                .modal_content{
                    width: 100%;
                    height: 80%;
    
            }
            .project_container{
                grid-template-columns: 1fr;
            }
        }
        }
        
